.wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 110px;
}
.container {
    margin-top: var(--dafault-layout-header-height);
    width: 100%;
    @media (min-width: 768px) {
        padding: 0 24px; // Tng khong m khi mn hnh rng hn tablet
    }

    @media (min-width: 1024px) {
        max-width: 1600px; // Gii hn chiu rng ti a khi mn hnh ln hn
        margin: 0 auto; // Cn gia container
        padding: 0; // Loi b khong m khi  c max-width
    }
}
.content {
    flex: 1;
}
